.testimonials {
  margin-bottom: 180px;

  &__container-card {
    display: flex;
    overflow: hidden;

    @include sm($screen-sm-max) {
      flex-direction: column;
    }
  }

  &__card {
    width: 400px;
    height: 230px;
    padding: 30px;
    margin:0px 0px 60px 50px;
    border: 2px solid #dddddd;
    border-radius: 10px;
  }

  &__card--active:hover {
    border: 2px solid $tertiary-color;
    box-shadow: 0px 0px 18px 10px rgba(0, 0, 0, 0.06);
    transition: 0.7s;
  }

  &__card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  &__info {
    padding: 0px 20px;
    flex-grow: 1;
  }

  &__points {
    margin-right: 10px;
  }

  &__buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__arrow-left {
    width: 60px;
    height: 60px;
    margin-right: 20px;
    padding: 18px;
    font-size: $lg-text;
    border: 2px solid $tertiary-color;
    border-radius: 50%;
    background: $white-color;
    color: $tertiary-color;
  }

  &__arrow-left--active:hover {
    background: $tertiary-color;
    transition: 0.5s;
  }

  &__arrow-right--active:hover {
    background: $tertiary-color;
    transition: 0.5s;
  }

  &__arrow-right {
    width: 60px;
    height: 60px;
    font-size: $lg-text;
    padding: 18px;
    border: 2px solid $tertiary-color;
    border-radius: 50%;
    background: $white-color;
    color: $tertiary-color;
  }

  &__dot {
    width: 15px;
    height: 15px;
    margin-right: 1.4em;
    border-radius: 50%;
    border: none;
    background: #dde0e4;
  }

  &__dot--active:hover {
    width: 45px;
    background-color: $tertiary-color;
    border: none;
    border-radius: 15%;
    transition: 0.5s;
  }
}
